:root {
  --text-color: #333;
  --text-color-secondary: #666;
  --background-color: #fff;
  --border-color: rgba(102, 102, 102, 0.5);
  --button-hover-opacity: 0.8;
}

.container {
  margin: 0;
  text-align: center;
  background-color: var(--background-color);
  padding-top: 20px;
  padding-bottom: 6.5vh;
}

.title {
  margin: 100px;
}

.header {
  font-size: 4em;
  color: var(--text-color);
  margin-bottom: 20px;
}

.header span {
  color: blue;
}

.top-description,
.bottom-description {
  color: var(--text-color-secondary);
}

.top-description {
  font-size: 2em;
  margin-bottom: 70px;
}

.bottom-description {
  font-size: 1.5em;
  line-height: 1.5;
  margin: 75px;
  white-space: pre-line;
}

.bottom-description p {
  margin: 0;
}

ul {
  margin: 120px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}

.card {
  background-color: var(--background-color);
  color: var(--text-color);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card div {
  border-radius: 10px;
  padding: 50px;
  margin: 50px;
  border: 1px solid var(--border-color);
}

.card p {
  margin-top: 0;
  margin-bottom: 50px;
}

.button {
  padding: 15px 20px;
  margin: 10px;
  color: #fff;
  border: none;
  border-radius: 5px;
  text-decoration: none;
  cursor: pointer;
  transition: background-color 0.3s;
}

.button1 {
  background-color: #007bffab;
}

.button1:hover {
  background-color: #0056b3;
}

.button2 {
  background-color: #28a746bd;
}

.button2:hover {
  background-color: #1e7e34;
}

.button3 {
  background-color: #dc3546b2;
}

.button3:hover {
  background-color: #bd2130;
}
