.footer {
  height: 6.5vh;
  width: 100%;
  background-color: #f8f9fa;
  color: #6c757d;
  border-top: 1px solid #dee2e6;
  position: relative;
}

.content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
}

.links a {
  margin-left: 1rem;
  color: black;
  text-decoration: none;
}

.rights {
  text-align: center;
  padding: 0.5rem 2rem;
  font-size: 0.875rem;
}
