.header {
  height: 6.5vh;
  width: 100%;
  display: flex;
  align-items: center;
  background-color: white;
  border-bottom: 1px solid #e8e8e8;
  justify-content: space-between;
}

.list {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.list a {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: black;
}

.list a:hover,
.list a.active {
  color: #4b61cd;
  vertical-align: middle;
}
.list a.active .img {
  stroke: #4b61cd;
}

.logo {
  vertical-align: middle;
  height: 45px;
}

.profile {
  vertical-align: middle;
  height: 35px;
  margin-right: 10px;
}

.img {
  vertical-align: middle;
  margin-left: 40px;
  margin-right: 8px;
}

.login {
  margin-right: 50px;
  color: black;
}
